/*********************************************************
* General
*/
pre[class*=language-],
code[class*=language-] {
  color: #bfc7d5;
  font-size: 16px;
  text-shadow: none;
  font-family: Fira Code, Menlo, Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  line-height: 2;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

pre[class*=language-]::selection,
code[class*=language-]::selection,
pre[class*=language-]::mozselection,
code[class*=language-]::mozselection {
  text-shadow: none;
  background: #50fc49;
}

@media print {
  pre[class*=language-],
code[class*=language-] {
    text-shadow: none;
  }
}
pre[class*=language-] {
  overflow: auto;
}

:not(pre) > code[class*=language-] {
  padding: 0.1em 0.3em;
  border-radius: 0.3em;
  color: #d2dcee;
}

/*********************************************************
* Tokens
*/
.namespace {
  opacity: 0.7;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #697096;
}

.token.punctuation {
  color: #bfc7d5;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #e2526d;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #feca72;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #bf8fe0;
}

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #c694e8;
}

.token.function {
  color: #88d6f5;
}

.token.regex,
.token.important,
.token.variable {
  color: #c4e791;
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

/*********************************************************
* Line highlighting
*/
pre[data-line] {
  position: relative;
}

pre[class*=language-] > code[class*=language-] {
  position: relative;
  z-index: 1;
}

.line-highlight {
  position: absolute;
  left: 0;
  right: 0;
  padding: inherit 0;
  background: rgba(65, 96, 147, 0.17);
  z-index: 0;
  margin-top: 1em;
  pointer-events: none;
  line-height: inherit;
  white-space: pre;
}

