    @apply .w-1/2;
  }
}
.docsearch-input__wrapper {
  @apply absolute;
  @apply bg-white;
  @apply mt-6;
  @apply left-0;
  @apply top-0;
  @apply px-4;
  @apply w-full;
  @apply z-10;
